<template>
  <div class="projects">
    <div align="left">
      <h1>Ideas</h1>
      <ul>
        <li>Use 'Media' instead of 'Cards' to display information?</li>
        <li>Remove pagination, use scroll only (keep search tho)</li>
        <li>Add overlay over projects as long as figures are loading?</li>
      </ul>
    </div>
    <b-container align-v="center">
      <b-card-group deck>
        <card
            v-for="info in projectsInfo"
            :key="info.id"
            :name="info.name"
            :description="info.text"
        ></card>
      </b-card-group>
    </b-container>
  </div>
</template>

<script>
  // @ is an alias to /src
  import CardProject from "@/components/CardProject.vue";
  import {mapGetters} from "vuex";

  export default {
    name: "Projects",
    components: {
      card: CardProject
    },
    computed: {
      ...mapGetters(["projectsInfo"])
    },
    mounted() {
      this.fetchData();
    },
    methods: {
      async fetchData() {
        await this.$store.dispatch("fetchProjects");
      }
    }
  };
</script>
